* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}
input {
  font-size: 1.2rem;
  width: 100%;
  color: black;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-right: 10px;
}
.card p {
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 15px;
  border-radius: 6px;

}
